.HeartButton {
  letter-spacing: 1px;
  font-size: 16px;
  text-transform: uppercase;
  padding: 12px 16px;
  background: none;
  color: var(--color-black);
  border: 1px solid var(--color-gray);
  border-radius: 8px;
}

.HeartButton:hover {
  background-color: var(--color-light-gray);
}

.HeartButton:disabled {
  background-color: var(--color-gray);
  cursor: not-allowed;
}
